.card {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 200px;
  background-color: #fff;
  padding: 32px 16px 16px;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
}

.card__image {
  align-self: center;
  margin-bottom: 40px;
  width: 160px;
  height: 134px;
}

.card__title {
  margin-bottom: 4px;
  font-weight: 500;
  font-size: 12px;
  line-height: 150%;
  color: var(--main-text-color);
}

.card__product-code {
  font-weight: 400;
  font-size: var(--text-size_s);
  line-height: 14px;
  color: var(--second-text-color);
  margin-bottom: 16px;
}

.card__reviews {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.card__number-rewiews {
  font-weight: 400;
  font-size: var(--text-size_s);
  line-height: 140%;
}

.card__price {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 16px;
}

.card__price-text {
  font-size: 12px;
  line-height: 18px;
  color: var(--second-text-color);
}

.card__price-count {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: var(--main-text-color);
}

.card__button {
  box-sizing: border-box;
  padding: 11px 0;
  background: var(--blue-color);
  color: #fff;
  border-radius: 5px;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  border: 1px solid var(--blue-color);
}

.card__button:hover {
  background-color: #fff;
  color: var(--blue-color);
}
