* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;

  --main-text-color: #060b35;
  --second-text-color: #616070;
  --blue-color: #00acdc;
  --text-size_s: 10px;

}

body {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  padding: 0;
  margin: 0;
}

h2,
p {
  margin: 0;
  line-height: 0;
}
